<template>
  <div class="home py-3">
    <!--<div id="map" style="width: 100%; height: 800px;"></div>-->

    <h1 class="text-center mb-4">口罩哪裡買</h1>

    <div class="input-group input-group-lg mb-3">
      <div class="input-group-prepend">
        <button
          class="btn btn-outline-dark dropdown-toggle"
          type="button"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
        >{{ (from.search.type === 'address') ? '地址' : '藥局名稱' }}</button>
        <div class="dropdown-menu">
          <a class="dropdown-item" @click="from.search.type = 'address'">地址</a>
          <a class="dropdown-item" @click="from.search.type = 'name'">藥局名稱</a>
        </div>
      </div>
      <input
        type="text"
        class="form-control border-dark"
        v-model="from.search.value"
        :placeholder="(from.search.type === 'address') ? 'ex: 高雄市前鎮區' : 'ex: 大樹藥局'"
        @keyup.enter="onSearch"
      />
      <div class="input-group-append">
        <button class="btn btn-dark" type="button" @click="onSearch">搜尋</button>
      </div>
    </div>

    <div class="row">
      <div class="col-12 mb-4">
        <div class="input-group mb-1">
          <div class="input-group-prepend">
            <div class="input-group-text">
              <input type="checkbox" v-model="from.filter.chechbox" @change="changeFilter" />
            </div>
          </div>
          <select class="custom-select" v-model="from.filter.value" @change="onSelectFilter">
            <option value selected>進階搜尋...</option>
            <option value="q_suff_50">庫存充足 (總量 &gt; 50)</option>
            <option value="q_suff_20">庫存有限 (總量 &gt; 20)</option>
            <option value="q_suff_10">庫存不足 (總量 &lt; 20)</option>

            <option value="a_suff_30">成人庫存 &gt; 30</option>
            <option value="a_suff_20">成人庫存 &gt; 20</option>
            <option value="a_suff_10">成人庫存 &lt; 10</option>

            <option value="c_suff_30">小孩庫存 &gt; 30</option>
            <option value="c_suff_20">小孩庫存 &gt; 20</option>
            <option value="c_suff_10">小孩庫存 &lt; 10</option>
          </select>
        </div>
      </div>
    </div>

    <div v-if="isLoading" class="row mt-5">
      <div class="col-12">
        <div class="spinner-border text-info" role="status">
          <span class="sr-only">Loading...</span>
        </div>
      </div>
    </div>
    <div v-else class="row mt-4">
      <div class="col-md-6" v-for="store in stores" :key="store.code">
        <div class="store-card mb-4">
          <div class="store-card-body">
            <div class="d-flex justify-content-between align-items-center mb-3">
              <span class="badge px-2 py-1" :class="getMaskTotalLevel(store.adult_count + store.child_count)">{{ getMaskTotalLevelText(store.adult_count + store.child_count) }}</span>
              <div class="update-time text-muted">{{ getUpdateTime(store.updated_time) }}更新</div>
            </div>
            <h4 class="card-title">
              {{ store.name }}
              <small class="card-sub-title">{{ getBusinessHour(store.business_hours) }}</small>
            </h4>
            <p class="store-address mb-4">
              <span class="mr-1">{{ store.address }}</span> (<i class="fa fa-location-arrow" aria-hidden="true"></i> {{ store.distance }} 公尺)
            </p>

            <div class="mask-stock adult mb-2">
              <div class="label">大人</div>
              <div class="value">{{ store.adult_count }}</div>
            </div>
            <div class="mask-stock child">
              <div class="label">小孩</div>
              <div class="value">{{ store.child_count }}</div>
            </div>
          </div>
          <div class="store-card-footer">
            <a class="link" :href="getPhone(store.phone)">
              <i class="fa fa-phone mr-1"></i>
              撥打電話
            </a>
            <a class="link" target="_blank" :href="getGoogleMap(store.lat, store.lng)">
              <i class="fa fa-paper-plane mr-1"></i>
              導航前往
            </a>
          </div>
        </div>
      </div>

    </div>

    <modal v-if="isShowModal" :width="'450px'" :hideFooter="true">
      <div
        slot="header"
        class="d-flex flex-row w-100 justify-content-between align-items-center px-2"
      >
        <b>{{ modelStore }} - 營業時間</b>
        <button type="button" class="close" aria-label="Close" @click="isShowModal = false">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div slot="body">
        <ul class="list-group list-group-flush">
          <li class="list-group-item py-2" v-for="(bh, k) in businessHours" :key="'bh' + k">{{ bh }}</li>
        </ul>
      </div>
    </modal>
  </div>
</template>

<script>
// import mapboxgl from "mapbox-gl/dist/mapbox-gl.js";

const Modal = () => import("@/components/Modal");

export default {
  name: "home",
  components: {
    Modal
  },
  data() {
    return {
      isLoading: false,
      coords: {
        lat: 0,
        lng: 0
      },
      map: null,
      from: {
        search: {
          type: "address",
          value: ""
        },
        filter: {
          chechbox: false,
          value: ""
        }
      },
      orgStores: [],
      stores: [],
      isShowModal: false,
      businessHours: [],
      modelStore: "",
      weekday: moment().format('e'),
      dayHour: parseInt(moment().format('HH'))
    };
  },
  mounted() {
    this.mapInit();
    this.searchLocation();
  },
  methods: {
    async searchLocation() {
      const hasLoc = await this.getLocation();
      if (!hasLoc) return;

      this.isLoading = true;

      const res = await this.$api.location(this.coords.lng, this.coords.lat);

      this.orgStores = res.data;
      this.stores = res.data;

      this.from.filter.chechbox = false;
      this.from.filter.value = "";
      this.onFilter();

      this.isLoading = false;
    },
    async onSearch() {
      this.isLoading = true;

      const res = await this.$api.search(
        this.from.search.type,
        this.from.search.value,
        this.coords.lng,
        this.coords.lat
      );

      this.orgStores = res.data;
      this.stores = res.data;

      this.from.filter.chechbox = false;
      this.from.filter.value = "";
      this.onFilter();

      this.isLoading = false;
    },
    getLocation() {
      return new Promise((resolve, reject) => {
        if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(p => {
            this.coords.lng = p.coords.longitude;
            this.coords.lat = p.coords.latitude;
            resolve(true);
          });
        } else {
          reject(false);
        }
      });
    },
    /*async mapInit() {
      const hasLoc = await this.getLocation();
      if (!hasLoc) return;

      mapboxgl.accessToken =
        "pk.eyJ1Ijoicm95YWxtYXIiLCJhIjoiY2p2NTV2N3YwMnkzdDN5cGZuMjgzNm56ayJ9.nFhZNElKWpm8zkQ-9mQM0Q";

      this.map = new mapboxgl.Map({
        container: "map",
        style: "mapbox://styles/mapbox/streets-v9?optimize=true",
        center: [this.coords.lng, this.coords.lat],
        minZoom: 6,
        maxZoom: 18,
        zoom: 13
      });

      this.setMarker(this.coords, { color: "#e91e63" });
    },
    setMarker(coords, options = {}, popup = null) {
      new mapboxgl.Marker(options).setLngLat(coords).addTo(this.map);
    },*/
    changeFilter() {
      if (!this.from.filter.chechbox) {
        this.from.filter.value = "";
        this.onFilter();
      }
    },
    onSelectFilter() {
      this.from.filter.chechbox = this.from.filter.value ? true : false;
      this.onFilter();
    },
    onFilter() {
      this.stores = this.orgStores;

      if (this.from.filter.value === "q_suff_50") {
        this.stores = this.stores.filter(store => {
          let count = store.adult_count + store.child_count;
          return count >= 50;
        });
      } else if (this.from.filter.value === "q_suff_20") {
        this.stores = this.stores.filter(store => {
          let count = store.adult_count + store.child_count;
          return count >= 20 && count < 50;
        });
      } else if (this.from.filter.value === "q_suff_10") {
        this.stores = this.stores.filter(store => {
          let count = store.adult_count + store.child_count;
          return count < 10;
        });
      } else if (this.from.filter.value === "a_suff_30") {
        this.stores = this.stores.filter(store => {
          return store.adult_count >= 30;
        });
      } else if (this.from.filter.value === "a_suff_20") {
        this.stores = this.stores.filter(store => {
          return store.adult_count >= 20 && store.adult_count < 30;
        });
      } else if (this.from.filter.value === "a_suff_10") {
        this.stores = this.stores.filter(store => {
          return store.adult_count < 10;
        });
      } else if (this.from.filter.value === "c_suff_30") {
        this.stores = this.stores.filter(store => {
          return store.child_count >= 30;
        });
      } else if (this.from.filter.value === "c_suff_20") {
        this.stores = this.stores.filter(store => {
          return store.child_count >= 20 && store.child_count < 30;
        });
      } else if (this.from.filter.value === "c_suff_10") {
        this.stores = this.stores.filter(store => {
          return store.child_count < 10;
        });
      }

      this.storeSort();
    },
    storeSort() {
      if (!this.coords.lng || !this.coords.lat) return;

      this.stores.sort((a, b) => {
        return a.distance - b.distance;
      });
    },
    getMaskTotalLevel(count) {
      if (count >= 50) return "badge-success";
      else if (count >= 20 && count < 50) return "badge-warning";
      else return "badge-danger";
    },
    getMaskTotalLevelText(count) {
      if (count >= 50) return "# 庫存充足";
      else if (count >= 20 && count < 50) return "# 庫存有限";
      else return "# 庫存不足";
    },
    getBusinessHour(businessHours) {
      if (!businessHours) return '';

      businessHours = businessHours.filter(b => b.indexOf(this.getWeekday()) !== -1);
      if (!businessHours) return '';

      let ft = '上午';
      if (this.dayHour >= 12 && this.dayHour < 17) {
        ft = '下午';
      }
      else if (this.dayHour >= 17 && this.dayHour < 24) {
        ft = '晚上';
      }

      businessHours = businessHours.filter(b => b.indexOf(ft) !== -1);
      if (!businessHours) return '';
      return businessHours[0];
    },
    getWeekday() {
      const weekdays = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五' , '星期六'];
      return weekdays[this.weekday];
    },
    getPhone(phone) {
      phone = phone.replace('(', '');
      phone = phone.replace(')', '');
      phone = phone.replace('-', '');

      return 'tel:' + phone;
    },
    getGoogleMap(lat, lng) {
      if (!this.coords.lng || !this.coords.lat) {
        return `https://www.google.com.tw/maps/dir/${lat},${lng}`;
      } else {
        return `https://www.google.com.tw/maps/dir/${this.coords.lat},${this.coords.lng}/${lat},${lng}`;
      }
    },
    getUpdateTime(createtime) {
      let now = moment(new Date()).format("X");
      let updateTime = moment(createtime).format("X");

      var limit = now - updateTime;
      var content = "";
      if (limit < 60) {
        content = limit + " 秒前";
      } else if (limit >= 60 && limit < 3600) {
        content = Math.floor(limit / 60) + " 分鐘前";
      } else {
        content = Math.floor(limit / 3600) + " 小時前";
      }

      return content;
    },
    showModal(name, bhs) {
      this.modelStore = name;
      this.businessHours = bhs || [];
      this.isShowModal = true;
    }
  }
};
</script>

